import React from 'react';

class PatientInfo extends React.Component {

    render() {
        console.log("patient Info ", this.props.patient);
        return <div className="container patient-info">
                    <h5 className="display-4">Patient Information</h5>
                    <table className="table">
                        <thead>
                            <tr className="row">
                                {this.props.patient ? (Object.keys(this.props.patient).map( (value) => (<th className="col">{value}</th>))) : "N/A" }
                            </tr>
                        </thead>
                        <tbody>
                            <tr className="row">
                                <td className="col">{this.props.patient.Name}</td>
                                <td className="col">{this.props.patient.Gender}</td>
                                <td className="col">{Array.isArray(this.props.patient.Addresses) ? this.props.patient.Addresses.map(v => Object.values(v).filter(a => a ).join(", ")) : "N/A"}</td>
                                <td className="col">{this.props.patient.Active ? "Yes" : "No" }</td>
                                <td className="col">{this.props.patient.BirthDate}</td>
                                <td className="col">{Array.isArray(this.props.patient.Phone) ? this.props.patient.Phone.map(p => p.Number + " (" + p.Type + ")"  ).join("<br/>") : "N/A"}</td>
                                <td className="col">{Array.isArray(this.props.patient.EmergencyContacts) ? this.props.patient.EmergencyContacts.map(v => (v.Name + " (" + v.RelationShip + ")")): "N/A"}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>;
    }
}

export default PatientInfo;
